


@media screen and (min-width:1024px) {


    .showcase__project{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 40vw;
        height: auto;
        margin: 4rem;
        padding: 2rem;
        border-radius: 1rem;
        background-color: rgb(235, 232, 232);
        box-shadow: 0 0.5rem 0.7rem 0.5rem lightgray inset, 0 0 0.5rem 0.2rem lightgray;
        animation: 0.8s ease spread;
             
        &-title{
            font-size: 2rem;
            margin: 2rem;
            background: none;
            animation: 1s ease fade-in 
        }
        &-img{
            width: 20rem;
            height: auto;
            border-radius: 1rem;
            box-shadow: 0 0.7rem 1rem 0.5rem lightgray inset, 0 0 1rem 1rem lightgray;
            padding: 2rem;
            animation: 1s ease fade-in 

        }
        &-tagline{
            background: none;
            animation: 1s ease fade-in 
        }
        &__button-wrapper{
            background: none;
            animation: 1s ease fade-in 
        }
    }
} 